<template>
  <div id="Home">
    <!---->
    <headerBack :title="title"></headerBack>
    <!---->
    <logoSearch></logoSearch>
    <!---->
    <bScroll
    class="wrapper"
    ref="scroll">
      <!---->
      <banner></banner>
      <!---->
      <studentClass ></studentClass>
      <!---->
      <showCourse ></showCourse>
      <!--   -->
      <div  v-for="(item,key) in listInfo" :key="key"  v-if="item.courselist.length!=0">
        <itemTitle  :title="item.categoryname" :category_id="item.category_id"></itemTitle>
        <fullItem :data="item.courselist"></fullItem>
      </div>
      <!---->
      <MealItem></MealItem>
      <!---->
      <famousTeacher></famousTeacher>
      <!---->
      <contactUs></contactUs>
    </bScroll>
  </div>
</template>
<script>
  import bScroll from '@/components/Lib/BScroll';           //bScroll
import headerBack from '@/components/HeaderBack';     //
import studentClass from './components/StudentClass'; //
import showCourse from './components/ShowCourse';   //
import fullItem from './components/FullItem';     //
import itemTitle from './components/ItemTitle';   //
import logoSearch from './components/LogoSearch';   //
import MealItem from './components/MealItem';   //
import banner from './components/Banner';   //
import famousTeacher from './components/FamousTeacher';   //
import contactUs from './components/ContactUs';   //
import Server from '@/service/request';
import Path from '@/service/Path';
export default{
  name:'Home',
  components:{
    bScroll,
    headerBack,
    studentClass,
    showCourse,
    fullItem,
    itemTitle,
    banner,
    contactUs,
    famousTeacher,
    logoSearch,
    MealItem
  },
  data(){
    return{
      listInfo:[],  //    
      selected:1,
      // 
      teacherRouter:'TeacherList',   //
      famousTeacher:'',
      title:'',
      itemTitle:'',
    }
  },
  created() {
    // 
    this.getTopCategoryCourser();
  },
  methods:{
    // 
    getTopCategoryCourser(){
      // 
      Server.post(Path.getTopCategoryCourser, {
      }, (res) => {
        if (res.code === 0) {
          // 
          this.listInfo = res.data;
        }
      });
    },
    // 
    getSlideList(){

    },

  },

}
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  #Home {
    @extend .main-child;
  }
</style>



