// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import '@/assets/common/common.css';   //
import '@/assets/common/reset.css';     //
import Vue from 'vue'
import App from './App'
import store from '@/store';
import router from './router'
import 'lib-flexible'     //
// import Fastclick from 'fastclick';
import { JSEncrypt } from 'jsencrypt'; //
import Utils from './assets/js/utils';
import defaultCover from './assets/images/defaultClass.png';      //
import errorLogo from './assets/images/errorLogo.png';      //
// 
import VueLazyLoad from 'vue-lazyload'
Vue.use(VueLazyLoad,{
  error:defaultCover,
  loading:defaultCover,
  attempt: 3
});

// 
import VeeValidate,{ Validator } from 'vee-validate';
// import VeeValidate from 'vee-validate';
import zh_CN from 'vee-validate/dist/locale/zh_CN'
import VueI18n from 'vue-i18n';
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'zh_CN',
})
Vue.use(VeeValidate, {
  i18n,
  i18nRootKey: 'validation',
  dictionary: {
    zh_CN
  }
});

const dictionary = {
  zh_CN: {
    messages:{
      required: (value) => ''
    }
  }
};

// Override and merge the dictionaries
Validator.localize(dictionary);


Validator.extend('phone', {
  getMessage: (field, [args]) => ``,
  validate: (value, [args]) =>{
    const reg = /^[0-9]{6,15}$/;
    return reg.test(value)
  }
});



Vue.prototype._imageError = function (e) { //
  e.target.src = errorLogo;
};
// uid
Vue.prototype.$uid = (JSON.parse(window.localStorage.getItem('appstudentinfo')) || {}).uid;
// 
Vue.config.productionTip = false;

// 300ms
// if ('addEventListener' in document) {
//   document.addEventListener('DOMContentLoaded', function () {
//     Fastclick.attach(document.body);
//   }, false);
// }


// 
const checkLogined = ()=>{
// 
  if(
    localStorage.getItem('appstudentinfo') == null ||
    localStorage.getItem('appstudentinfo') == "" ||
    localStorage.getItem('appstudentkey') == null ||
    localStorage.getItem('appstudentkey') == ""
  ) {
    localStorage.removeItem('appstudentinfo');    //
    localStorage.removeItem('appstudentkey');     //key
    localStorage.removeItem('code');     //key
    // router.push('/Login');
    return false;
  }else{
    return true;
  }
};

// 
router.beforeEach((to, from, next) => {
  if (checkLogined()) {
    let ua = navigator.userAgent.toLowerCase();
    let code = localStorage.getItem('code');
    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
      // login  register     /Author 
      if (!code && (to.path == '/' || to.path == '/Home')) {
        // 
        // let obj = {
        //   path:to.name,
        //   query:to.query
        // };
        // let objInfo = JSON.stringify(obj);
        // localStorage.setItem('beforeLoginUrl', objInfo);
        // 
        next({path: '/Author'});
        return false;
      }
    }
  }

    if (checkLogined()) {
      if(to.path=='/Home'){
        next('/');
        return;
      }
      next();
    } else {
      if(to.path=='/Login'  || to.path=='/Register'){
        next()
      }else{
        next({path: 'Login'})
      }
    }

})





Vue.prototype.$loca = window.localStorage;        //local
Vue.prototype.$timeOut = null;        //
Vue.prototype.$Utils = Utils;
Vue.prototype.$uid = (JSON.parse(window.localStorage.getItem('appstudentinfo')) || {}).uid;       //ID

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



