<template>
  <!---->
  <div class="ShowOrder">
    <!---->
    <headerBack title=""></headerBack>
    <!---->
    <courseImg :imgUrl="orderInfo.setimgpath"></courseImg>
    <!---->
    <div class="orderInfo">
      <ul>
        <li>:{{orderInfo.ordernum}}</li>
        <li>:{{timestampToTime(orderInfo.ordertime)}}</li>
        <li>:{{orderInfo.setmeal}}</li>
        <li>:{{orderInfo.content}}</li>
      </ul>
    </div>
    <!---->
    <bottomButton :buttonL="''+orderInfo.amount"   buttonR="" leftColor="rgba(235,28,28,1)" @selectedTab="selectedTab"  backgroundL="rgba(255,255,255,1)" backgroundR="rgba(57,173,239,1)"></bottomButton>
    <!--Modal-->
    <paymentModal v-if="modalStatus" @priceStatus="priceStatus"  :balances="orderInfo.amount"  :price="orderInfo.usablemoney"></paymentModal>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //
  import bottomButton from '@/components/BottomButton';     //
  import {Toast} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import courseImg from '@/components/CourseImg';     //
  import paymentModal from '@/components/PaymentModal';   //Modal
  let that = {};
  export default{
    name:"ShowOrder",
    components:{
      headerBack,
      bottomButton,
      courseImg,
      paymentModal
    },
    data(){
      return {
        orderNum:'',  //
        orderInfo:{}, //
        modalStatus:false,  //modal
        paytype:'',     //   1  2
        weiInfo:{},     //
        studentid:'',   //ID
        code:'',
      }
    },
    created(){
      // ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      that = this;
      // 
      this.orderNum = this.$route.query.orderNum;
      // 
      this.showOrderDetail();
    },
    methods:{
      // 
      timestampToTime(timestamp) {
        var date = new Date(timestamp * 1000);//10*1000131000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y+M+D+h+m+s;
      },
      // 
      showOrderDetail(){
        Server.post(Path.showOrderDetailPackage,{
          'ordernum':this.orderNum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 
            this.orderInfo = data;
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 
      selectedTab(value){
        if(value=='right'){
          //   Modal
          this.modalStatus = true;
        }
      },
      // 
      priceStatus(value,balance){
        let code = localStorage.getItem('code');
        this.paytype = balance;
        // modal
        this.modalStatus = false;
        if(value=='yes'){
          Server.post(Path.gotoPay,{
            'ordernum':this.orderNum,
            'paytype':this.paytype,
            'studentid':this.studentid,
            'code':code
          },(res)=>{
            let { code, data } = res;
            if(res.code===0){
              if(this.paytype==1){
                this.$router.push('/PaymentBack');
              }else{
                this.weiInfo = res.data;
                this.pay();
              }
            }else{
              Toast({
                message: res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }
          })
        }else{
          console.log('');
        }
      },
      // 
      pay(){
        if (typeof WeixinJSBridge == "undefined"){
          if( document.addEventListener ){
            document.addEventListener('WeixinJSBridgeReady',that.onBridgeReady,false);
          }else if (document.attachEvent){
            document.attachEvent('WeixinJSBridgeReady', that.onBridgeReady);
            document.attachEvent('onWeixinJSBridgeReady', that.onBridgeReady);
          }
        }else{
          that.onBridgeReady();
        }
      },
      onBridgeReady(){
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId":that.weiInfo.appId,     //
            "timeStamp":that.weiInfo.timeStamp,         //1970
            "nonceStr":that.weiInfo.nonceStr, //
            "package":that.weiInfo.package,
            "signType": that.weiInfo.signType,         //:
            "paySign": that.weiInfo.paySign   //
          },
          function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok"){
              that.$router.push('/PaymentBack');
            }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
              Toast('');
            }else{
              Toast('');
            }
          });
      }
    },
  }
</script>
<style scoped lang="scss">
  .ShowOrder{
    position:absolute;



    top:0;
    right:0;
    left:0;
    bottom:0;
    background:rgba(255,255,255,1);
    /**/
    .orderInfo{
      margin-top:20px;
      li{
        height:88px;
        padding-left:40px;
        line-height:88px;
        font-size:30px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(61,61,61,1);
        border-bottom:1px solid rgba(217,217,217,1);
      }
    }
  }
</style>



