<template>
  <!---->

  <div class="DataEditing">
    <!---->
    <headerBack title=""  v-if="from=='register'" typeStatus="tittle" rightTitle=""   @clickMe="toPage('top')"></headerBack>
    <headerBack title=""  v-if="from=='mineCard'" typeStatus="tittle" ></headerBack>
    <bScroll
      class="wrapper"
      ref="scroll"
    >
    <div>
      <div class="infoContent">
        <!--Item-->
        <div class="imgItem" >
          
          <div class="imgOut">
            <img :src="imgUrl"  @error="_imageError" width="100%" height="100%" alt="">
          </div>
        </div>
        <!--Item-->
        <div class="itemItem" @click="toAutograph('nickname',nickname)">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">{{nickname}}</div>
          </div>
        </div>
        <!---->
        <div class="itemItem"  @click="openToast">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis" v-if="sex==1"></div>
            <div class="titleSpan text-ellipsis" v-if="sex==2"></div>
          </div>
        </div>

        <!---->
        <div class="itemItem" @click="openPicker">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">{{birth}}</div>
          </div>
        </div>
        <!---->
        <div class="itemItem"  @click="popupCountry=true;">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">
              <!--<span style="padding-right:3px;">{{countryName}}</span>-->
              <!--<span style="padding-right:3px;">{{provinceName}}</span>-->
              <!--<span style="padding-right:3px;">{{cityName}}</span>-->
              <span style="padding-right:3px;">{{getCity(country)}}</span>
              <span style="padding-right:3px;">{{getCity(province)}}</span>
              <span style="padding-right:3px;">{{getCity(city)}}</span>
            </div>
          </div>
        </div>
        <!---->
        <router-link to="/AddressSelect">
          <div class="itemItem"   v-if="from=='mineCard'">
            
            <div class="rightTitle ">
              <div class="rightIcon">
                <span class="iconSpan">
                </span>
              </div>
              <div class="titleSpan text-ellipsis" v-if="addressNow">
                {{addressNow.pname}} {{addressNow.cname}}  {{addressNow.aname}}
              </div>
            </div>
          </div>
        </router-link>
        <!---->
        <div class="itemItem" @click="toPhone"  v-if="from=='mineCard'">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">
              +<span>{{prphone}}</span>
              {{mobile}}
            </div>
          </div>
        </div>

        <!---->
        <div class="itemItem" v-if="from=='mineCard'" @click="changePass">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
          </div>
        </div>
        <!---->
        <div class="itemItem"   @click="toAutograph('autograph',profile)">
          
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">{{profile}}</div>
          </div>
        </div>
      </div>
      <!--<div class="saveButton" v-if="from=='mineCard'"  @click="toPage">-->
        <!---->
      <!--</div>-->
      <div style="height:40px;"></div>
    </div>
    </bScroll>
    <middleToast v-if="toastStatus==true">
      <div class="toastDiv">
        <div class="sex">
          
        </div>

        <div class="radio">
          <div class="sexName">
            
          </div>
          <div class="sexMan"></div>
          <div @click="changeSex(1)"  :class="{'buttonNone':sex!=1,'buttonOk':sex==1}" ></div>
        </div>
        <div class="radio" style="border:none;">
          <div class="sexName">
            
          </div>
          <div class="sexWoman"></div>
          <div @click="changeSex(2)"  :class="{'buttonNone':sex!=2,'buttonOk':sex==2}"></div>
        </div>
      </div>
    </middleToast>
    <!---->
    <mt-popup v-model="popupCountry" position="right" style="width:100%;height:100%;">
      <country :popupVisible="popupCountry"  @closeModal="closeCountry"  @selectCountry="selectCountry"></country>
    </mt-popup>
    <!---->
    <mt-popup v-model="popupProvince" position="right" style="width:100%;height:100%;">
      <province  :popupVisible="popupProvince" @provinceOk="provinceOk"  :countryID="country"  @selectProvince="selectProvince"  @closeModal="closeProvince"></province>
    </mt-popup>
    <!---->
    <mt-popup v-model="popupCity" position="right" style="width:100%;height:100%;">
      <city  :popupVisible="popupCity"  :provinceID="province"  @selectCity="selectCity"  @closeModal="closeCity"></city>
    </mt-popup>

    <!-- -->
    <mt-popup v-model="autographStatus" position="right" style="width:100%;height:100%;">
      <autograph @closeModal="closeAuto"  @saveValue="saveValue" :maxLength="maxLength" :value="autoValue"></autograph>
    </mt-popup>
    <!---->
    <mt-datetime-picker class="mydatepicker" ref="picker" type="date"
                        v-model="value"
                        :year-format="`{value} `"
                        :month-format="`{value} `"
                        :date-format="`{value} `"
                        :startDate="startDate"
                        :endDate="endDate"
                        @confirm="confirmDate">
    </mt-datetime-picker>
  </div>

</template>
<script>
  import headerBack from '@/components/HeaderBack';     //
  import middleToast from '@/components/MiddleToast';     //
  import country from '@/components/Country';     //
  import province from '@/components/Province';     //
  import city from '@/components/City';     //
  import autograph from '../Autograph';     //
  import bScroll from '@/components/Lib/BScroll';           //bScroll
  import {Popup,Toast,DatetimePicker} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import CityJSON from '@/assets/city/city.json';
  export default{
    name:'DataEditing',
    components:{
      bScroll,
      headerBack,
      middleToast,
      country,
      autograph,
      province,
      city,
      'mt-datetime-picker': DatetimePicker,
      'mt-popup':Popup
    },
    data(){
      return {
        autographStatus:false,  //
        maxLength:'',           //
        autoValue:"",            //
        from:"",        //
        studentid:'',       //ID
        popupCountry:false,    //
        popupProvince:false,   //
        popupCity:false,    //
        toastStatus:false,  //
        imgUrl:"",        //
        addressNow:{},   //

        country:'',       //ID
        province:'',      //ID
        city:'',          //ID
        value:'',
        startDate: new Date('1960-01-01'),
        endDate: new Date(),
        sex:1,
        profile:'',   //
        nickname:'',      //
        prphone:"86",
        mobile:'',
        birth: "",
      }
    },
    mounted(){

    },
    created(){
      this.from = this.$route.query.from;
      // ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 
      this.getStudentInfo();
      // 
      this.getAddressList();
    },
    methods:{
      // 
      getAddressList(){
        Server.post(Path.getAddressList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 
            if(data.length>0){
                // 
                for(let item of data){
                  if(item.isdefault==1){
                    this.addressNow = item;
                  }
                }

            }else{
              // 
              this.noneAddress = true;
            }
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 
      getStudentInfo(){
        Server.post(Path.getStudentInfo,{
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
              this.country=data.country;   //ID
              this.province= data.province;    //ID
              this.city= data.city;        //ID
              this.sex=data.sex;
              this.profile= data.profile; //
              this.nickname= data.nickname;     //
              this.imgUrl =  data.imageurl;     //
              this.prphone=data.prphone;
              this.mobile=data.mobile;
              this.birth=data.birth;
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      /**
       * *   
       */
      // 
      openPicker() {
        this.$refs.picker.open();
      },
      confirmDate(val) {
        this.birth = this.formatDate(val);
        this.toPage('');
      },
      formatDate(date) {
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        let d = date.getDate();
        d = d < 10 ? "0" + d : d;
        return y + "-" + m + "-" + d;
      },


      /**
       * *   
       */
      //
      getCity: function(val) {
        if(val == 0 || val == null){
          return
        }else{
          CityJSON.map((el,index) => {
            if(el.id == val){
              val =el.name
            }
          })
          return val;
        }
      },
      // 
      closeCountry(){
        this.popupCountry = false;
      },
      // 
      closeProvince(){
        this.popupProvince = false;
      },
      // 
      closeCity(){
        this.popupCity = false;
      },
      // ID
      selectCountry(value){
        // ID
        this.country = value;
        // 
        this.popupProvince = true;
      },
      // ID
      selectProvince(value){
        // ID
        this.province = value;
        // 
        this.popupCity = true;
      },
      // 
      selectCity(value){
        // 
        this.city = value;
        this.popupCity = false;
        this.popupProvince = false;
        this.popupCountry = false;
        this.toPage('');
      },
      provinceOk(value){
        this.province = value;
        this.city = 0;
        this.popupProvince = false;
        this.popupCountry = false;
        this.toPage('');
      },

      /**
       * *   
       */
      // 
      openToast(){
        this.toastStatus = true;
      },
      // 
      changeSex(sex){
        if(sex!=this.sex){
          this.sex = sex;
        }
        this.toastStatus = false;
        this.toPage('');
      },
      /**
       * *    
       */
      //  
      toAutograph(type,value){
        if(type=='nickname'){
          this.maxLength = 10;
        }else{
          this.maxLength = 100;
        }
        this.autoValue = value;
        this.autographStatus = true;
      },
      // 
      closeAuto(){
        this.autographStatus = false;
      },
      // 
      saveValue(value){
        if(this.maxLength==10){
          this.nickname = value;
        }else{
          this.profile = value;
        }
        this.autographStatus = false;
        this.toPage('');
      },
      /**
       * *   
       */
      // 
      toPage(value){
        Server.post(Path.updateStudentInfo,{
          'imageurl':'url',
          'nickname':this.nickname,
          'sex':this.sex,
          'country':this.country,
          'province':this.province,
          'city':this.city,
          'birth': this.birth,
          'profile':this.profile,
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            if(this.from=='mineCard'){
              Toast({
                message:'',
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }else{
              if(value=='top'){
                this.$router.push('/CourseScreening');
              }
            }

          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });

      },


      // 
      toPhone(){
        this.$router.push({path:'/ChangePhone',query:{'prphone':this.prphone,'mobile':this.mobile}});
      },
      // 
      changePass(){
        this.$router.push({path:'/ChangePass',query:{'prphone':this.prphone,'mobile':this.mobile}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0px;
    background:rgba(255,255,255,1);
  }
  .saveButton{
    width:300px;
    height:88px;
    line-height:88px;
    text-align: center;
    background:rgba(57,173,239,1);
    border-radius:8px;
    font-size:32px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    margin:72px auto 0px;
  }
  .DataEditing{
    background:rgba(255,255,255,1);
    /**/
    .toastDiv{
      width:548px;
      box-sizing: border-box;
      background:rgba(255,255,255,1);
      border-radius:12px;
      padding:47px 64px 60px 62px;
      .sex{
        height:61px;
        text-align: center;
        font-size:40px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
      }
      .radio{
        height:120px;
        box-sizing: border-box;
        border-bottom:1px solid rgba(217,217,217,1);
        padding-left:5px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        .sexName{
          height:119px;
          width:90px;
          float:left;
          line-height:119px;
          margin-right:32px;
        }
        .sexMan{
          height:34px;
          width:34px;
          margin-top:43px;
          float:left;
          @include bg-image("../assets/icon_man");
        }
        .sexWoman{
          height:34px;
          width:34px;
          margin-top:43px;
          float:left;
          @include bg-image("../assets/icon_woman");
        }
        .buttonNone{
          height:34px;
          width:34px;
          margin-top:43px;
          float:right;
          @include bg-image("../assets/icon_buttonNone");
        }
        .buttonOk{
          height:34px;
          width:34px;
          margin-top:43px;
          float:right;
          @include bg-image("../assets/icon_buttonOk");
        }

      }
    }
    .infoContent{
      padding:0px 41px 0px 39px;
      /*Item*/
      .itemItem{
        height:118px;
        line-height:118px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(61,61,61,1);
        border-bottom:1px solid rgba(217,217,217,1);
        /**/
        .rightTitle{
          height:118px;
          line-height: 118px;
          width:380px;
          float:right;
          text-align: right;
          .titleSpan{
            width:350px;
            float:right;
            height:118px;
            line-height: 118px;
            font-size:30px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(128,128,128,1);
            span{
              font-size:30px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(128,128,128,1);
            }
          }
          .rightIcon{
            width:24px;
            height:118px;
            line-height: 118px;
            float:right;
          }
          .iconSpan{
            font-size:0px;
            display:inline-block;
            width:12px;
            height:24px;
            @include bg-image("~@/assets/images/icon_right");
            background-size:100% 100%;
            margin-left:2px;
          }
        }
      }
      /*Item*/
      .imgItem{
        height:170px;
        line-height:170px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(61,61,61,1);
        border-bottom:1px solid rgba(217,217,217,1);
        /**/
        .imgOut{
          width:120px;
          height:120px;
          line-height:120px;
          float:right;
          overflow:hidden;
          margin-top:25px;
          border-radius:50%;
        }
      }
    }
  }
</style>



