<template>
  <!---->
  <div class="MyNews myCellSwipe">
    <headerBack title=""></headerBack>
    <mt-navbar :value="selected" v-model="selected" >
      <mt-tab-item id="1" value="1"></mt-tab-item>
      <mt-tab-item id="2"></mt-tab-item>
    </mt-navbar>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="1" v>
            <div
              infinite-scroll-disabled="loading"
              infinite-scroll-distance="10"
              class="content"
            >
              <mt-cell-swipe class="orderItem" v-for="(item,key) in newsInfo" :key="key"
                             :right="[
                      {content: '',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.id,key)}
                      ]" >
                <myNewsItem :data="item"></myNewsItem>
              </mt-cell-swipe>

            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div
              infinite-scroll-disabled="loadingReceive"
              infinite-scroll-distance="10"
              class="content"
            >
              <mt-cell-swipe class="orderItem" v-for="(item,key) in courseInfo" :key="key"
                             :right="[
                      {content: '',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.id,key)}
                      ]" >
                <myNewsItem :data="item"></myNewsItem>
              </mt-cell-swipe>

            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <notInfo v-if="infoStatus">
      
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //
  import myNewsItem from '../components/MyNewsItem';      // item
  import notInfo from '@/components/NotInfo';           //
  import { Loadmore,Navbar, TabItem ,TabContainer, TabContainerItem,CellSwipe} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyNews',
    components:{
      headerBack,
      myNewsItem,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-loadmore':Loadmore,
      'mt-cell-swipe':CellSwipe,
    },
    data(){
      return{
        selected:'1',
        studentid:'',     //ID
        newsInfo:[],   //
        courseInfo:[],  //

        infoStatus:false,  //
        total: 0,         //
        limit: 10,        //
        pagenum:1,        //
        allLoaded: false, //
      }
    },
    created(){
      // ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      if(window.localStorage.getItem('selectedNews') && window.localStorage.getItem('selectedNews')==2){
        this.selected = window.localStorage.getItem('selectedNews');
        return;
      }
      // 
      this.messageList();
    },
    methods:{
      /**
       * 
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //loading
          this.pagenum = 1;                      //1
          this.allLoaded = false;             //
          this.messageList();                //
        }, 500);
      },
      /**
       * 
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //
          this.allLoaded = true;                              //
          this.$refs.loadmore.onBottomLoaded();               //loading
        } else {                                              //
          setTimeout(() => {
            this.pagenum++;                                      //
            this.messageList();
            this.$refs.loadmore.onBottomLoaded();             //loading
          }, 500);
        }
      },
      // 
      messageList(){
        Server.post(Path.messageList,{
          'studentid':this.studentid,
          'pagenum':1,
          'limit':10,
          'type':this.selected
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.limit;
            if (this.pagenum != 1 ) {
              if(this.selected==='2'){
                this.courseInfo = this.courseInfo.concat(data.data);
              }else{
                this.newsInfo = this.newsInfo.concat(data.data);
              }
            } else {
              if(this.selected==='2'){
                this.courseInfo = data.data;
              }else{
                this.newsInfo = data.data;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 
      deleteItem(id,key){
        Server.post(Path.deleteMsg,{
          'messageids':id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 
            if(this.selected=='1'){
              this.newsInfo.splice(key,1);
              // 
              if(this.newsInfo.length==0){
                this.infoStatus = true;
              }
            }else{
              this.courseInfo.splice(key,1);
              // 
              if(this.courseInfo.length==0){
                this.infoStatus = true;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    },
    watch:{
      selected:function(value){
        // 
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedNews', this.selected);
        this.pagenum = 1;
        // 
        this.messageList();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyNews{

  }
</style>



