<template>
  <!---->
  <div>
    <!---->
    <headerBack title=""></headerBack>
    <verticalScroll>
      <!---->
      <teacherInfo @teacherCollect="teacherCollect" :data='teacherInfo'></teacherInfo>
      <!---->
      <div class="TeacherInfo">
        <!--item-->
        <circleBlock :title="teacherInfo.sex==1?'':''"></circleBlock>
        <!--<verticalScroll :data="courseInfo" scrollType="Tab">-->
          <fullItem :data="courseInfo"  :teacherid="teacherInfo.teacherid"  :show-status="0"></fullItem>
        <!--</verticalScroll>-->
      </div>
    </verticalScroll>

    <notInfo v-if="infoStatus">
      
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //
  import teacherInfo from '../components/TeacherInfo';        //
  import fullItem from '../components/FullItem';     //
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import circleBlock from '@/components/CircleBlock';     //
  import notInfo from '@/components/NotInfo';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  export default{
    name:'TeacherInfo',
    components:{
      headerBack,
      teacherInfo,
      fullItem,
      circleBlock,
      verticalScroll,
      notInfo
    },
    data(){
      return{
        infoStatus:false, // 
        teacherInfo:{},   //
        studentid:'',  //ID
        teacherid:'',  //ID
        // 
        courseInfo:[
        ]
      }
    },
    created() {
      // ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // ID
      this.teacherId = this.$route.query.teacherId;
      // 
      this.getTeacherCurriculum(this.teacherId);
      // 
      this.getTeacherClass(this.teacherId);
    },
    methods:{
      // 
      getTeacherCurriculum(id){
        // 
        Server.post(Path.getTeacherCurriculum, {
          'teacherid':id
        }, (res) => {
          if (res.code === 0) {
            this.teacherInfo = res.data;
          }
        });
      },
      // 
      getTeacherClass(id){
        // 
        Server.post(Path.getTeacherClass, {
          'teacherid':id,
          'classtype':2
        }, (res) => {
          console.log(res.data);
          if (res.code === 0) {
            if(res.data.data.length==0){
              this.infoStatus = true;
            }else{
              this.courseInfo = res.data.data;
            }

          }
        });
      },
      // 
      teacherCollect(){
        Server.post(Path.teacherCollect, {
          'teacherid':this.teacherId,
          'studentid':this.studentid
        }, (res) => {
          if (res.code === 0) {
            this.teacherInfo.is_collect = 1;
            Toast('');
          }
        });
      }
    },
    mounted(){

    }
  }
</script>
<style lang="scss" scoped>
.TeacherInfo{
  margin-top:-23px;
  background:rgba(255,255,255,1);
  border-radius:20px 20px 0px 0px;
  position:relative;
  overflow:hidden;
}

</style>



