<template>
  <div class="courseSelect">
    <!---->
    <headerBack title=""></headerBack>
    <bScroll
      class="wrapper"
      ref="scroll"
    >
    <div>
      <div class="payment">
        <div class="payTitle">
          <img  v-if="listInfo.classinfo[classIndex]" v-lazy="listInfo.classinfo[classIndex].imageurl" alt=""  class="payImg"/>
          <div class="payP">
            <p class="title"></p>
            <p class="payNum" v-if="listInfo.classinfo">{{listInfo.classinfo[classIndex].totalprice}}</p>
          </div>
        </div>
        <div v-if="classtypes==2">
          <div class="payDate"></div>
          <div class="paySelect">
            <select  @change="changeTime" style="width:100%;height:100%"  ref="selectDate">
              <option  v-for="(item,key) in dateInfo" :key="key"    :selected="key==dateIndex" >{{item.date}}</option>
            </select>
            <img :src="selectt" alt=""  class="selecttImg"/>
          </div>
          <div class="payType"></div>
          <ul class="payUlOne" v-if="dateInfo[dateIndex] && dateInfo[dateIndex].class">
            <li  @click="changeVs(info)" :class="{'payLiOne':fullpeople==info,'payOne':fullpeople!=info}"   v-for="(info,k) in dateInfo[dateIndex].class"  :key="k">1v{{info}}</li>
          </ul>
          <div class="payClass"></div>
          <ul class="payUlTwo" v-if="listInfo.classinfo">
            <li @click="changeClass(key,item.applystatus)"   v-if="key==0 || listInfo.classinfo[key-1].applystatus==0"   v-for="(item,key) in listInfo.classinfo" class="payLiTwop" :class="{'payLiTwo':key==classIndex,'payLiTwoNo':key!=classIndex}" :key="key">
              <p>{{item.gradename}}</p>
            </li>
          </ul>
          <!---->
          <div v-if="listInfo.giftstatus==0">
            <div class="sendSome"></div>
            <label v-for="(item,key) in listInfo.giftdescribe">{{item.name}}x{{item.num}}&nbsp;&nbsp;</label>
          </div>
        </div>
      </div>
      <!---->
      <div v-if="classtypes==2 && listInfo.giftstatus==0">
        <div class="addressTitle">
          
        </div>
        <div class="addressContent">
          <div class="address" v-if="!noneAddress">
            <div class="topAddress">
              <div class="leftTitle text-ellipsis">
                {{addressNow.linkman}}
              </div>
              <div class="phoneNum">
                {{addressNow.mobile}}
              </div>
              <div class="rightBlock" v-if="addressNow.isdefault==1">
                
              </div>
            </div>
            <div class="bottomAddress">
              {{addressNow.pname}}{{addressNow.cname}}{{addressNow.aname}}{{addressNow.address}}
            </div>
          </div>
          <div class="noAddress" v-if="noneAddress">
            
          </div>
          <!--<router-link :to="{path:'/AddressSelect',query: {from:'course','date':this.date,'fullpeople':this.fullpeople,'courseid':this.courseid,'classtypes':this.classtypes,'dateIndex':this.dateIndex}}" >-->
            <div class="rightIcon" @click="toSelectAddress">
              <div class="iconShow"></div>
            </div>
          <!--</router-link>-->


        </div>
      </div>
      <div v-if="listInfo.classinfo[classIndex].totalprice*listInfo.classinfo[classIndex][this.changeName] && userPakeInfo.length>0">
        <div class="addressTitle">
          
        </div>
        <radio @selectRadio="selectRadio" :data="userPakeInfo"></radio>
      </div>
      <div style="border-top:1px solid rgba(217,217,217,1);padding-top:10px;">
        <ul >
          <li>
            <div class="leftItem">
              
            </div>
            <div class="rightItem">
              {{toDecimal2(listInfo.classinfo[classIndex].totalprice*listInfo.classinfo[classIndex][this.changeName])}}
            </div>
          </li>
          <li>
            <div class="leftItem">
              
            </div>
            <div class="rightItem">
               &nbsp;{{this.discountNum}}  
            </div>
          </li>
          <li>
            <div class="leftItem">
              
            </div>
            <div class="rightItem">
              {{toDecimal2(realNum)}}
            </div>
          </li>
        </ul>
      </div>
    </div>
    </bScroll>
    <div class="paySign"  @click="gotoOrderCourse"  v-if="buttonStatus==1"></div>
    <div class="paySign"  @click="MyEndInfo"  v-if="buttonStatus==2"></div>
    <div class="paySigns"   v-if="!buttonStatus"></div>
  </div>
</template>
<script>
  import selectt from '@/assets/images/select.png';
  import headerBack from '@/components/HeaderBack';     //
  import bScroll from '@/components/Lib/BScroll';           //bScroll
  import radio from '@/components/Radio';     //
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Radio,Toast} from 'mint-ui';
  export default{
    name:'courseSelect',
    components:{
      headerBack,
      'mt-radio':Radio,
      radio,
      bScroll,
    },
    data(){
      return {
        date:'',      //
        dateIndex:'',   //
        fullpeople:'',  //
        courseid:'',   //ID
        classIndex:0,  //
        classtypes:'',  //   2  1
        dateInfo:[],    //
        radioSelect:0,  //
        studentid:'',   //ID
        listInfo:{
          classinfo:[{
            imageurl:''
          },
            {
              imageurl:''
            }]
        },    //
        userPakeInfo:[],  //
        radioInfo:[],   //
        pageStatus:false,  //
        buttonStatus:false,  //
        selectt,  //
        addressInfo:[],  //
        addressNow:{},  //
        addressid:'',   //ID
        key:'',       //
        noneAddress:false,  //
        discountNum:0,    //
        realNum:0,     //
        changeName:'',  //
      }
    },
    created(){

      // 
      this.getAddressList();
      // ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 
      this.date = this.$route.query.date;                 //
      this.fullpeople = this.$route.query.fullpeople;       //
      this.courseid = this.$route.query.courseid;         //ID
      this.classtypes = this.$route.query.classtypes;     //  1 2
      if(this.classtypes==2){
        this.changeName = 'lessons_num';
      }else{
        this.changeName = 'periodnum';
      }
      this.key = this.$route.query.key;               //
      this.classIndex = this.$route.query.classIndex;   //ID
      // 
      if(this.classtypes==1){
        this.getCurriculumInfo();
      }else{
        // 
        this.getCurriculumDateList();
      }


    },
    methods:{
      toDecimal2(x) {
        var f = parseFloat(x);
        if (isNaN(f)) {
          return false;
        }
        var f = Math.round(x * 100) / 100;
        var s = f.toString();
        var rs = s.indexOf('.');
        if (rs < 0) {
          rs = s.length;
          s += '.';
        }
        while (s.length <= rs + 2) {
          s += '0';
        }
        return s;
      },
      // 
      MyEndInfo(){
        this.$router.push({path:'/MyEndInfo',query:{'selected':this.classtypes,'schedulingid':this.listInfo.classinfo[this.classIndex].scheduid,'courseid':this.courseid}});
      },
      // 
      toSelectAddress(){
        if(this.noneAddress){
          // 
          this.$router.push('AddAddress');
        }else{
          this.$router.replace({path:'/AddressSelect',query: {from:'course','date':this.date,'fullpeople':this.fullpeople,'courseid':this.courseid,'classtypes':this.classtypes,'classIndex':this.classIndex,'dateIndex':this.dateIndex}});
        }
      },
      getAddressList(){
        Server.post(Path.getAddressList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 
            if(data.length>0){
              this.addressInfo = data;
              // 
              if(typeof(this.key)=='number'){
                // 
                this.addressNow = this.addressInfo[this.key];
                // ID
                this.addressid = this.addressNow.id;
                return;
              }else{
                // 
                // 
                let defaultStatus = false;
                for(let item of data){
                  if(item.isdefault==1){
                    this.addressid = item.id;
                    this.addressNow = item;
                    // 
                    defaultStatus = true;
                  }
                }
                // 
                if(!defaultStatus){
                  this.addressid = data[0].id;
                  this.addressNow = data[0];
                }
              }
            }else{
                // 
              this.noneAddress = true;
            }
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 
      getCurriculumDateList(){
        Server.post(Path.getCurriculumDateList, {
          'courseid':this.courseid,
        }, (res) => {
          if (res.code === 0) {
            if(res.data.length!=0){
              for(let i in res.data){
                // 
                if(res.data[i].date == this.date){
                  this.dateIndex = i;
                }
              }
            }
            // 
            this.dateInfo = res.data;
            // 
            this.getCurriculumInfo();
          }
        });
      },
      // 
      getCurriculumInfo(){
        Server.post(Path.getCurriculumInfo, {
          'date':this.classtypes==2?this.dateInfo[this.dateIndex].date:this.date,
          'fullpeople':this.fullpeople,
          'courseid':this.courseid,
          'classtypes':this.classtypes,
        }, (res) => {
          if (res.code === 0) {
            // 
            if(this.classtypes==1){
              // 
              this.listInfo.classinfo[0] = res.data;
            }else{
              this.listInfo = res.data;
              // 
              this.buttonStatus = this.listInfo.classinfo[this.classIndex].applystatus;
            }
            if(this.listInfo.classinfo.length!=0){
              // 
              this.realNum =  this.listInfo.classinfo[this.classIndex].totalprice*this.listInfo.classinfo[this.classIndex][this.changeName];
            }
            //  
            if(!this.pageStatus){
              this.getUserPackage();
              this.pageStatus = true;
            }
            // 
            this.buttonStatus = this.listInfo.classinfo[this.classIndex].applystatus;
          }else{
            Toast(res.info);
          }
        });
      },
      // 
      getUserPackage(){
        Server.post(Path.getUserPackage, {
          'curriculumid':this.courseid,
          'amount':this.listInfo.classinfo[this.classIndex].totalprice
        }, (res) => {
          if (res.code === 0) {
            this.userPakeInfo = res.data;
            for(let i in this.userPakeInfo){
              this.radioInfo.push(this.userPakeInfo[i].setmeal);
            }
          }
        });
      },
      // 
      changeTime(){
      // date
        let now = this.$refs.selectDate;
        for(let i in this.dateInfo){
          if(now.value==this.dateInfo[i].date){
            // 
            this.dateIndex = i;
            // 
            this.fullpeople = this.dateInfo[i].class[0];
            // 
            this.getCurriculumInfo();
            return;
          }
        }
      },
      // 
      changeClass(key,buttonStatus){
        this.classIndex = key;
        this.buttonStatus = buttonStatus;
      },
      // 
      changeVs(key){
        if(this.fullpeople!=key){
          this.fullpeople = key;
          // 
          this.getCurriculumInfo();
        }
      },
      // 
      selectRadio(index,num){
        // ID
        this.radioSelect = index;
        // 
        if(num>=this.listInfo.classinfo[this.classIndex][this.changeName]){
          //   
          this.discountNum = this.listInfo.classinfo[this.classIndex][this.changeName];
        }else{
          // 
          this.discountNum = num;
        }
        // 
        if(num>=this.listInfo.classinfo[this.classIndex][this.changeName]){
          //   0
            this.realNum = 0;
        }else{
          //  -*
          this.realNum = (this.listInfo.classinfo[this.classIndex][this.changeName]-num)*this.listInfo.classinfo[this.classIndex].totalprice;
        }
      },
      // 
      gotoOrderCourse(){
        // giftstatus
        console.log(this.classtypes);
        console.log(this.listInfo.giftstatus);
        console.log(this.listInfo.classinfo[this.classIndex]);
        if(!this.classtypes==2?this.listInfo.giftstatus:this.listInfo.classinfo[this.classIndex].giftstatus && this.addressid===''){
          Toast('');
          return;
        }
        Server.post(Path.gotoOrderCourse, {
          'courseid':this.courseid,     //ID
          'studentid':this.studentid,   //ID
          'schedulingid':this.listInfo.classinfo[this.classIndex].scheduid,  //id
          'amount':this.realNum.toFixed(2),   //
          'ordersource':3,//
          'originprice':(this.listInfo.classinfo[this.classIndex].totalprice*this.listInfo.classinfo[this.classIndex][this.changeName]).toFixed(2),      //
          'addressid':this.addressid,      //ID
          'usestatus':this.discountNum?1:0,   // 1 0   
          'packageid':this.discountNum?this.userPakeInfo[this.radioSelect].packageid:'',//ID
          'packagegiftid':this.discountNum?this.userPakeInfo[this.radioSelect].packagegiftid:'',  //ID
          'packageuseid':this.discountNum?this.userPakeInfo[this.radioSelect].packageuseid:'',  //ID
        }, (res) => {
          if (res.code === 0) {
            console.log(res.data.ordernum);
            // 
            this.$router.push({path:'SignUp',query:{ordernum:res.data.ordernum}});
          }else{
            Toast(res.info);
          }
        });
      }
    }
  }
</script>
