<template>
  <!---->
  <div class="MyCourse">
    <headerBack title=""  typeStatus="tittle" rightTitle="" @clickMe="clickMe"></headerBack>
    <div >
      <mt-navbar :value="selected" v-model="selected" >
        <mt-tab-item id="2" ></mt-tab-item>
        <mt-tab-item id="1"></mt-tab-item>
      </mt-navbar>
    </div>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="2">
            <div  infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content">
              <!---->
              <myCourseNow :data="beginInfo"  @MyEndInfo="MyEndInfo"></myCourseNow>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div  infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <!---->
              <myCourseEnd :data="endInfo"  @MyEndInfo="MyEndInfo"></myCourseEnd>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <mt-popup
      v-model="popupVisible"
      position="bottom"  style="width:100%">
      <div class="bottomPopup">
        <div class="offIcon" @click="closeModal">
          <img src="~@/assets/images/icon_add.png" width="100%" alt="">
        </div>
        <!---->
        <div class="selectTop">
          
          <div class="selectOption">
            <select style="width:100%;height:40px;" v-model="oldIndex" @change="changeOld"  >
              <option v-if="!newStatus" value="0" disabled></option>
              <option   v-for="(item, index) in listInfo" :key="index" :value="index">{{item.gradename}}</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <!---->
        <div class="subItem">
          <span style="margin-right:20px;">
            
          </span>
          <span v-if="periodnum">
            {{periodnum}}
          </span>
        </div>
        <!---->
        <div class="selectTop">
          
          <div class="selectOption">
            <select style="width:100%"  v-model="newIndex" @change="changeNew"  >
              <option value="0" disabled ></option>
              <option v-if="newStatus" :value="newIndex"  v-for="(info,key) in listInfo[oldIndex].classlist" :key="key">{{info.gradename}}</option>
              <option v-if="newStatus && listInfo[oldIndex].classlist.length==0" :value="newIndex"    disabled></option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <!---->
        <div class="subItem" v-if="timeStatus">
          <span style="margin-right:20px;">
            
          </span>
          <span >
            {{teachername}}{{starttime}}-{{endtime}}
          </span>
        </div>

        <div class="buttonOut">
          <div style="width:50%;float:left;">
            <div class="leftButton" @click="okModal"></div>
          </div>
          <div style="width:50%;float:left;">
            <div class="rightButton"  @click="closeModal"></div>
          </div>

        </div>
      </div>
    </mt-popup>

    <middleToast v-if="toastStatus==true" >
      <div class="infoContent">
        <div class="topIcon">
          <img src="~@/assets/images/icon_success.png" height="100%" alt="">
        </div>
        <div class="titleInfo">
          
        </div>
        <div class="infoInfo">
          12
        </div>
        <div class="okOut">
          <div class="okShow" @click="closeToast">
            
          </div>
        </div>
      </div>
    </middleToast>

    <middleToast v-if="toastType==true" >
      <div class="infoContent">
        <div class="topIcon">
          <img src="~@/assets/images/icon_paymentError.png" height="100%" alt="">
        </div>
        <div class="titleInfo">
          
        </div>
        <div class="infoError">
          :
          1.
          2.
          3.023-6235196
        </div>
        <div class="okOut">
          <div class="okShow" @click="toastType=false;">
            
          </div>
        </div>
      </div>
    </middleToast>

    <notInfo v-if="infoStatus">
      
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //
  import myCourseNow from '../components/MyCourseNow';      // 
  import myCourseEnd from '../components/MyCourseEnd';      // 
  import middleToast from '@/components/MiddleToast';     //
  import notInfo from '@/components/NotInfo';           //
  import { Loadmore,Navbar,Popup,Toast, TabItem ,TabContainer, TabContainerItem} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyCourse',
    components:{
      headerBack,
      myCourseNow,
      myCourseEnd,
      middleToast,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-popup':Popup,
      'mt-loadmore':Loadmore
    },
    data(){
      return{
        studentid:'',         //ID
        toastStatus:false,  //
        toastType:false,   //
        popupVisible:false, //
        beginInfo:[],       //
        endInfo:[],        //
        selected:'2',//    1   2
        listInfo:[
          {
            classlist:[]
          }
        ],   //
        timeStatus:false,  //
        oldIndex:0,   //
        newIndex:0,   //
        periodnum:'',    //
        newStatus:false,  //
        teachername:'',  //
        starttime:'',   //
        endtime:'',     //
        infoStatus:false,  //
        total: 0,         //
        limit: 10,        //
        pagenum:1,        //
        allLoaded: false, //

      }
    },
    created(){
      // ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      if(window.localStorage.getItem('selectedCourse') && window.localStorage.getItem('selectedCourse')==1){
        this.selected = window.localStorage.getItem('selectedCourse');
        return;
      }
      // 
      this.getBuyCurriculum();
    },
    methods:{
      // 
      getBuyCurriculum(){
        Server.post(Path.getBuyCurriculum,{
          'studentid':this.studentid,
          'pagenum':this.pagenum,
          'limit':this.limit,
          'coursetype':this.selected
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              if(this.selected==='2'){
                this.beginInfo = this.beginInfo.concat(data.data);
              }else{
                this.endInfo = this.endInfo.concat(data.data);
              }
            } else {
              if(this.selected==='2'){
                this.beginInfo = data.data;
              }else{
                this.endInfo = data.data;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      /**
       * 
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //loading
          this.pagenum = 1;                      //1
          this.allLoaded = false;             //
          this.getBuyCurriculum();                //
        }, 500);
      },
      /**
       * 
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //
          this.allLoaded = true;                              //
          this.$refs.loadmore.onBottomLoaded();               //loading
        } else {                                              //
          setTimeout(() => {
            this.pagenum++;                                      //
            this.getBuyCurriculum();
            this.$refs.loadmore.onBottomLoaded();             //loading
          }, 500);
        }
      },
      // 
      MyEndInfo(schedulingid,courseid){
          this.$router.push({path:'/MyEndInfo',query:{'selected':this.selected,'schedulingid':schedulingid,'courseid':courseid}});
      },
      /**
       * 
       */
      // 
      clickMe(){// 
        Server.post(Path.getAllClassList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              Toast('');
            }else{
              // 
              this.listInfo = data;
              this.popupVisible = true;
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 
      changeOld(){
        this.timeStatus = false;
        // 
        this.newStatus = true;
        // 
        this.periodnum = this.listInfo[this.oldIndex].periodnum;
      },
      // 
      changeNew(){
        // 
        this.timeStatus = true;
        this.teachername = this.listInfo[this.oldIndex].classlist[this.newIndex].teachername;
        this.starttime = this.listInfo[this.oldIndex].classlist[this.newIndex].starttime;
        this.endtime = this.listInfo[this.oldIndex].classlist[this.newIndex].endtime;
      },
      // 
      closeModal(){
        // 
        this.timeStatus = false;
        this.newStatus = false;
        this.popupVisible = false;
      },
      // 
      okModal(){
        if(!this.timeStatus &&  !this.newStatus){
          Toast('');
        }else if(!this.timeStatus){
          Toast('');
        }else{
          Server.post(Path.submitApplyClasss,{
            'curriculumid':this.listInfo[this.oldIndex].curriculumid,
            'oldschedulingid':this.listInfo[this.oldIndex].schedulingid,
            'newschedulingid':this.listInfo[this.oldIndex].classlist[this.newIndex].schedulingid
          },res => {
            let {code, data} = res;
            if (code === 0) {
              // 
              this.popupVisible = false;
              // 
              this.toastStatus = true;
            }else{
              this.popupVisible = false;
              this.toastType = true;
            }
          })

        }
      },
      // toast
      closeToast(){
        this.toastStatus = false;
      }
    },
    watch:{
      selected:function(value){
        // 
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedCourse', this.selected);
        this.pagenum = 1;
        // 
        this.getBuyCurriculum();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyCourse{
    /**/
    .infoContent{
      position:relative;
      width:580px;
      background:rgba(255,255,255,1);
      border-radius:10px;
      padding:20px 55px 20px 55px;
      z-index:6;
      .topIcon{
        height:64px;
        text-align:center;
        margin-bottom:20px;
      }
      .titleInfo{
        height:48px;
        text-align:center;
        font-size:30px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(51,51,51,1);
      }
      .infoInfo{
        font-size:26px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(51,51,51,1);
        line-height:40px;
        margin-bottom:47px;
      }
      .okOut{
        height:52px;
        display:flex;
        justify-content: center;
        .okShow{
          width:122px;
          height:52px;
          line-height: 52px;
          text-align: center;
          border:1px solid rgba(63,156,232,1);
          border-radius:20px;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(63,156,232,1);
        }
      }
    }
    .bottomPopup{
      height:787px;
      box-sizing: border-box;
      padding:95px 40px 77px 39px;
      background:rgba(255,255,255,1);
      border-radius:12px 12px 0px 0px;
      position:relative;
      /**/
      .offIcon{
        position:absolute;
        top:21px;
        right:25px;
        width:19px;
        height:19px;
        transform: rotate(45deg);
      }
      /**/
      .selectTop{
        height:88px;
        line-height: 88px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        margin-bottom:62px;
        .selectOption{
          box-sizing: border-box;
          padding-left:20px;
          float:right;
          width:500px;
          position:relative;
          height:88px;
          background:rgba(250,250,252,1);
          border:1px solid rgba(217,217,217,1);
          border-radius:4px;
          font-size:28px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(77,77,77,1);
          .rightBottom{
            position:absolute;
            transform: rotate(90deg);
            height:20px;
            width:20px;
            top:32px;
            right: 22px;
          }
        }
      }
      .buttonOut{
        height:86px;
        margin-top:79px;
        .leftButton{
          height:86px;
          line-height: 86px;
          text-align: center;
          width:254px;
          background:rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(255,255,255,1);
          margin:0px auto;
        }
        .rightButton{
          width:254px;
          height:86px;
          line-height: 86px;
          text-align: center;
          border:2px solid rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(57,173,239,1);
          margin:0px auto;
        }
      }
      .subItem{
        height:30px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:33px;
        margin-bottom:63px;
      }
    }
  }
</style>



